const filledStar = <StarIcon color="success" size={8} />
const emptyStar = <StarEmptyIcon size={8} />

const starStyle = {
  width: "100%",
  textAlign: "center",
  whiteSpace: "nowrap"
}

const stars = {
  tick: <div style={starStyle}><TickCircleIcon color="success"/></div>,
  one: <div style={starStyle}>{filledStar}{emptyStar}{emptyStar}{emptyStar}{emptyStar}</div>,
  two: <div style={starStyle}>{filledStar}{filledStar}{emptyStar}{emptyStar}{emptyStar}</div>,
  three: <div style={starStyle}>{filledStar}{filledStar}{filledStar}{emptyStar}{emptyStar}</div>,
  four: <div style={starStyle}>{filledStar}{filledStar}{filledStar}{filledStar}{emptyStar}</div>,
  five: <div style={starStyle}>{filledStar}{filledStar}{filledStar}{filledStar}{filledStar}</div>
}

