export class SkillsGrid extends React.Component {

  render() {
    return (
      <Grid
        data={tableData}
        columns={[{
          id: 'title',
          name: 'Name',
          formatter: (cell) => {
            let desiredObj = tableData.filter(obj => obj.title === cell)[0];
            let cellType = desiredObj ? cellTypes[desiredObj.cellType] : "";
            return _(<p>{cell} {cellType}</p>)
          }
        }, {
          id: 'level',
          name: _(<ChartIcon />),
        }, {
          id: 'description',
          name: 'Description'
        }]}
        search={true}
        pagination={{
          enabled: true,
          limit: 10
        }}
        className={{
          table: "custom-table"
        }}
      />
    )
  };
}

