import React from "react";
import { Dialog, Button, InfoSignIcon } from 'evergreen-ui';

import SummaryInfo from './SummaryInfo'

class Summary extends React.Component {

  state = {
    isShown: false
  }

  togglePane = (isShown) => this.setState({ isShown });

  render() {
    return (
    <React.Fragment>
      <Dialog
        isShown={this.state.isShown}
        title="Summary"
        onCloseComplete={() => this.togglePane(false)}
        hasFooter={false}
        height="80%"
        width="75%"
      >
        <SummaryInfo />
      </Dialog>
      <Button style={{color: "black"}} height={32} intent="none" iconBefore={InfoSignIcon} onClick={() => this.togglePane(true)} margin={8}>
            Summary
      </Button>
    </React.Fragment>
  )};
}

export default Summary