import React from "react";
import { Pane, Tab, Tablist } from 'evergreen-ui';

// Custom components
import About from '../about/About';
import Articles from '../articles/Articles';
import Projects from '../projects/Projects';

class HomeTabs extends React.Component {
  state = {
    selectedIndex: 0,
    tabs: ['About', 'Projects', 'Articles']
  }

  availableTabs = {
    About: <About />,
    Projects: <Projects />,
    Articles: <Articles />
  }

  render () {
    return (
    <Pane>
      <Tablist marginTop={24} marginBottom={16} flexBasis={240} marginRight={24}>
        {this.state.tabs.map((tab, index) => (
          <Tab
            style={{
              color: "white"
            }}
            height="2em"
            key={tab}
            id={tab}
            onSelect={() => this.setState({ selectedIndex: index })}
            isSelected={index === this.state.selectedIndex}
            aria-controls={`panel-${tab}`}
          >
            {tab}
          </Tab>
        ))}
      </Tablist>
      <Pane padding={16} background="tint1" flex="1">
        {this.state.tabs.map((tab, index) => (
          <Pane
            key={tab}
            id={`panel-${tab}`}
            role="tabpanel"
            aria-labelledby={tab}
            aria-hidden={index !== this.state.selectedIndex}
            display={index === this.state.selectedIndex ? 'block' : 'none'}
          >
            {this.availableTabs[tab]}
          </Pane>
        ))}
      </Pane>
    </Pane>
  )}
}

export default HomeTabs