import React from "react";
import { Button } from 'evergreen-ui';

import { InlineIcon } from '@iconify/react';

import telegramIcon from '@iconify/icons-logos/telegram';
import facebookIcon from '@iconify/icons-logos/facebook';
import linkedinIcon from '@iconify/icons-cib/linkedin';
import twitterIcon from '@iconify/icons-logos/twitter';
import devIcon from '@iconify/icons-fa-brands/dev';
import githubIcon from '@iconify/icons-fa-brands/github';
import habrIcon from '@iconify/icons-cib/habr';
import hryvniaaltIcon from '@iconify/icons-whh/hryvniaalt';

const links = [
  { icon: githubIcon, title: "Github", url: "https://github.com/dmytrohoi", color: "black" },
  { icon: twitterIcon, title: "Twitter", url: "https://twitter.com/dmytrohoi" },
  { icon: linkedinIcon, title: "Linkedin", url: "https://linkedin.com/in/dmytrohoi", color: "#0e76a8" },
  { icon: devIcon, title: "Dev.To", url: "https://dev.to/dmytrohoi", color: "black" },
  { icon: habrIcon, title: "Habr.com", url: "https://habr.com/users/dmytrohoi", color: "#89aab8" },
  { icon: telegramIcon, title: "Telegram", url: "https://t.me/dmytrohoi" },
  { icon: facebookIcon, title: "Facebook", url: "https://fb.com/dmytrohoi" },
]

const buttons = [
  { icon: telegramIcon, title: "higaindev", url: "https://t.me/higaindev", indent: "none" },
  { icon: hryvniaaltIcon, title: "Donate", url: "https://dmytrohoi.com/donate", indent: "warning" },
]

export default function LinksPane () {
    return (
    <div>
      {links.map(link => (
        <Button is="a" href={link.url} target="blank" appearance="minimal" height={32} intent="none" margin={4} key={link.title}>
          <InlineIcon width="1.25em" height="1.25em" icon={link.icon} color={link.color}>{link.title}</InlineIcon>
        </Button>
      ))}
      <br />
      {buttons.map(button => (
        <Button is="a" href={button.url} target="blank" appearance="minimal" height={32} intent={button.indent} margin={4} key={button.title}>
          <InlineIcon width="1.25em" height="1.25em" icon={button.icon} color={button.color} style={{marginRight: 8}} />
          {button.title}
        </Button>
      ))}
    </div>
    )
}
