
import React from "react";
import { Pane, Heading, LinkIcon, HelpIcon, Link, Icon, Badge, Tooltip } from 'evergreen-ui';

import './Articles.css'

// Tables
import { _, Grid } from 'gridjs-react';
import "gridjs/dist/theme/mermaid.min.css";

// Icons
import { InlineIcon } from '@iconify/react';
import articleLine from '@iconify/icons-ri/article-line';
import twotoneLanguage from '@iconify/icons-ic/twotone-language';


// Constants
export const articles = [
  {
    topic: "code",
    lang: "rus",
    title: "GitHub Actions как CI/CD для сайта на статическом генераторе и GitHub Pages",
    desc: "GitHub Actions as CI/CD for static website with GitHub Pages",
    date: "06.11.2019",
    link: "https://habr.com/ru/post/474668/"
  },
  {
    topic: "code",
    lang: "rus",
    title: "Параметризация из файла в py.test",
    desc: "Parametrization from file with py.test",
    date: "23.10.2019",
    link: "https://habr.com/ru/post/472766/"
  },
  {
    topic: "civic",
    lang: "ukr",
    title: "Студенти та бюрократія врятують університети",
    desc: "Students and bureaucracy will save universities",
    date: "21.10.2017",
    link: "https://studway.com.ua/studenti-ta-byurokratiya/"
  },
  {
    topic: "civic",
    lang: "ukr",
    title: "Чим запам’ятатися у виші, окрім імені у списку випускників?",
    desc: "How will can remember you in a higher education institution, except for the name on the graduate list?",
    date: "11.09.2017",
    link: "https://studway.com.ua/chim-zapamyatatisya/"
  },
  {
    topic: "civic",
    lang: "ukr",
    title: "Звертаюся до тебе, першокурснику!",
    desc: "I talk to you, freshman!",
    date: "09.08.2017",
    link: "https://studway.com.ua/pershokursniku/"
  },
  // { topic: "poems", title: "Check Poems", date: "10.10.2019", link: "" },
]

const topics = {
  code: {color: "green", description: "Articles about programming"},
  civic: {color: "yellow", description: "Articles on the topic of civic engagement"},
  poems: {color: "red", description: "Poems and other prose"}
}


export default function Articles() {
  return (
    <Pane
      flexWrap="wrap"
      wordWrap="break-word"
      padding={16}
    >

      <Pane>
        <Heading size={800} margin="default"><InlineIcon icon={articleLine} width={24} height={24} /> Articles</Heading>
      </Pane>

      <Pane>
        <Grid
          data={() => {
            return new Promise(resolve => {
              setTimeout(() =>
                resolve(articles), 2000);
            });
          }}
          columns={[{
            id: 'topic',
            name: "Topic",
            formatter: (topic) => {
              let topicInfo = topics[topic]
              return _(
                <Tooltip content={topicInfo.description}>
                  <Badge color={topicInfo.color}>{topic}</Badge>
                </Tooltip>
              )
            }
          }, {
            id: 'title',
            name: 'Title',
            formatter: (title) => {
              let articleInfo = articles.filter(article => article.title === title)[0];
              let link = articleInfo.link ?
                  <Link marginRight={12} is="a" href={articleInfo.link} target="blank">
                    <Icon height={24} icon={LinkIcon} />
                  </Link> : null
              let desc = articleInfo.desc ?
                <Tooltip content={articleInfo.desc}>
                  <Icon marginLeft={8} height={5} icon={HelpIcon} color="#8c8c8c" />
                </Tooltip> : null
              return _(<div>{link}{title}{desc}</div>)
            }
          }, {
            id: 'lang',
            name: _(<InlineIcon icon={twotoneLanguage}/>),
            formatter: lang => _(<Badge>{lang}</Badge>)
          }, {
            id: 'date',
            name: 'Date'
          }]}
          search={true}
          // pagination={{
          //   enabled: true,
          //   limit: 20
          // }}
          className={{
            container: "articles-container",
            table: "articles-table"
          }}
        />
      </Pane>
    </Pane>
  )
}
